<template>
    <div class="login">
        <div class="box">
            <img src="../assets/title.png" alt="">
        </div>
        <div class="det-login">
            <!-- @submit="" -->
            <van-form @submit="login">
                <van-field
                    v-model="loginUserInfo.phone"
                    name="手机号"
                    label="手机号"
                    placeholder="手机号"
                   class="tel input"
                />
                <van-field
                    v-model="loginUserInfo.password"
                    :type="!isOpenLogin ? 'password' : 'text'"
                    name="密码"
                    label="密码"
                    placeholder="密码"
                   :right-icon="!isOpenLogin ? 'closed-eye' : 'eye-o'"
                   class="input"
                   @click-right-icon="toggleLoginPasssword"
                />
                <!-- eye-o  closed-eye -->
                <div class="forgot">
                    <div class="forgot-pwd">忘记密码？</div>
                </div>


                <div style="margin: 35px 16px 16px 16px;">
                    <van-button round block type="info" native-type="submit">登录</van-button>
                </div>
                <div style="margin: 16px;">
                    <van-button round block type="info" @click="showPopup">注册</van-button>
                </div>
            </van-form>
        </div>
        <van-popup v-model="show" position="bottom"  :style="{ height: '40%' ,width:'100%'}">
            <div class="register-title">
                注册
            </div>
            <van-form @submit="register">
                <van-field
                    v-model="registerUserInfo.nickName"
                    name="昵称"
                    label="昵称"
                    placeholder="昵称"
                    class="tel"
                />
                 <van-field
                    v-model="registerUserInfo.phone"
                    name="手机号"
                    label="手机号"
                    placeholder="手机号"
                    class="tel"
                />
                <van-field
                    v-model="registerUserInfo.password"
                    :type="!isOpenRegister ? 'password' : 'text'"
                    name="注册密码"
                    label="注册密码"
                    placeholder="密码"
                    :right-icon="!isOpenRegister ? 'closed-eye' : 'eye-o'"
                    @click-right-icon="toggleResgiterPasssword"
                />
                <div style="margin: 25px 16px 16px 16px;">
                    <van-button round block type="info" native-type="submit">注册</van-button>
                </div>
            </van-form>
            </van-popup>
       <!-- Toast('提示内容'); -->
    </div>
</template>

<script>
    export default {
        name:'kogin',
        data(){
            return{
                show:false,
                Icon:'',
                falg:false,
                loginUserInfo:{
                    phone:'',
                    password:'',
                   
                },
                registerUserInfo:{
                    phone:'',
                    password:'',
                    nickName:'',
                },
                 //查看注册密码
                isOpenRegister: false,
                 //查看登录密码
                isOpenLogin:false,
                proId:'',
                proPge:'',
            }
        },
        // 加载前获取传递过来的参数
        created(){
            let str = this.$route.params
            // console.log(str);
            this.proId = str.id
            this.proPge = str.pge
        },
        methods:{
            showPopup() {
                this.show = !this.show;
            },
            //切换注册密码可见状态
            toggleResgiterPasssword() {
                this.isOpenRegister = !this.isOpenRegister;
            },
            //切换登录密码可见状态
            toggleLoginPasssword() {
                this.isOpenLogin = !this.isOpenLogin;
            },
            login(){
                let data='';
                for(let key in this.loginUserInfo){
                    data += `${key}=${this.loginUserInfo[key]}&`
                }
                data += 'appkey=U2FsdGVkX19WSQ59Cg+Fj9jNZPxRC5y0xB1iV06BeNA='
                  this.axios({
                    method:'post',
                    url:'http://www.kangliuyong.com:10002/login/',
                    data
                }).then(result=>{
                    // console.log(result);
                    this.$toast(result.data.msg);
                    if(result.data.code === 200){
                         //讲token保存到localStorage
                        localStorage.setItem('token', result.data.token);
                        setTimeout(()=>{
                            this.$router.push({name:'mymain'})
                        },1000)
                    }
                }).catch(err=>{
                    console.log(err);
                })
            },
            register(){
                // let appkey = 'U2FsdGVkX19WSQ59Cg+Fj9jNZPxRC5y0xB1iV06BeNA=';
                let data='';
                for(let key in this.registerUserInfo){
                    data += `${key}=${this.registerUserInfo[key]}&`
                }
                data += 'appkey=U2FsdGVkX19WSQ59Cg+Fj9jNZPxRC5y0xB1iV06BeNA='
                // console.log(data);
                this.axios({
                    method:'post',
                    url:'http://www.kangliuyong.com:10002/register/',
                    data
                }).then(result=>{
                    // console.log(result);
                    // Toast('提示内容');
                    // this.show = false
                    this.$toast(result.data.msg);
                    if (result.data.code === 100 || result.data.code === 102) {
                        this.show = false
                        //清除用户注册信息
                        for (let key in this.registerUserInfo) {
                            this.registerUserInfo[key] = '';
                        }
                    }
                }).catch(err=>{
                    // console.log(err);
                })
            },
        },
    }
</script>

<style lang="less" scoped>

    .login{
        width: 100%;
        height: 667px;
        background-color: #4CB7E1;
    }
    .box{
        // margin: 100px 0;
        position: absolute;
        top:25%;
        left: 23%;
    }
     .register-title{
        font-size: 20px;
        // margin-bottom: 5px;
        padding: 5px;
    }
    .det-login{
        position: absolute;
        top: 40%;
        left: 6%;
    }
    .input{
        border-radius: 5px;
    }
    .tel{
        margin-bottom: 10px;
    }
    .forgot{
        position: relative;
        .forgot-pwd{
            position: absolute;
            right: 0;
            top: 10px;
        }
        
    }
</style>